import { useState } from 'react'
import { UX, Component, Property, List, Item } from './FlexyComponent'
import { sync } from './flexy/MicroLitho'

const MicroLithoUX = () => {
  const [visible] = useState(true)
  const [list] = useState(['Item 1', 'Item 2'])

  return (
    <UX
      className="microLithoPage"
      root={sync}
      fit="height"
      backgroundColor="auto"
      borderColor="#222"
      adjustTextSize={false}
      animate={true}
    >
      <Component
        name="Summary Sheet"
        onClick={() => {
          console.log('click is fired on Summary Sheet 🔥')
        }}
        visibile={visible}
      ></Component>

      <List
        list="file-list"
        item="@item"
        scroll="vertical"
        as="ul"
        direction="column"
        gap={10}
      >
        {list.map((item, index) => (
          <Item
            key={index}
            onClick={() => {
              console.log(`${item} clicked!`)
            }}
          >
            <Property
              name="file-no"
              innerText={String(index + 1)}
              onClick={() => console.log('hello property')}
            />
            <Property name="file-name" innerText={item} />
          </Item>
        ))}
      </List>
    </UX>
  )
}

export default MicroLithoUX
